export function PostData(type, userData) {

    let BaseURL = 'http://14.98.73.11/6ixd/public/api/';

    return new Promise((resolve, reject) =>{

    fetch( BaseURL+type , {
            method: 'POST',
            body: JSON.stringify(userData)
    })
    
    .then((response) => response.json())
            .then((res) => {
                console.log('done');
                resolve(res);
    })

    .catch((error) => {
            reject(error);
        });
   });

}